/* main */
body {
  background-color: #f3f3f3; }

.container {
  width: 1330px;
  padding: 0 15px;
  margin: 0 auto;
  max-width: 100%; }

.truck-calculator {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between; }
  .truck-calculator__left {
    width: 180px; }
  .truck-calculator__center {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .truck-calculator__right {
    width: 300px; }

.car__head {
  width: 100%;
  display: flex;
  justify-content: center; }
  .car__head img {
    display: block; }

.car__body {
  width: 100%;
  display: flex;
  justify-content: center; }

.car__content {
  position: relative;
  background: pink; }
  .car__content::before {
    z-index: -1;
    width: 100%;
    height: 100%;
    pointer-events: none;
    content: "";
    border: 3px solid #333333;
    position: absolute;
    left: -3px;
    top: -3px;
    box-sizing: content-box; }
  .car__content-info {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    pointer-events: none; }
  .car__content-reels {
    position: relative;
    width: 100%;
    height: 100%;
    background: darkred; }

.car-info__value {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  font-family: sans-serif;
  text-align: center;
  font-weight: 600; }
  .car-info__value--height {
    left: -50px;
    top: 50%;
    transform: translateY(-50%) rotate(-90deg); }
  .car-info__value--width {
    bottom: -45px;
    left: 50%;
    transform: translateX(-50%); }
  .car-info__value--load {
    right: -50px;
    top: 0;
    transform: rotate(-90deg) translateX(30%); }

.car-info__line {
  position: absolute;
  background-color: #a7a7a7; }
  .car-info__line--height-top {
    top: 0;
    width: 50px;
    height: 1px;
    left: -50px; }
  .car-info__line--height-bottom {
    bottom: 0;
    width: 50px;
    height: 1px;
    left: -50px; }
  .car-info__line--height-left {
    height: 100%;
    width: 1px;
    top: 0;
    left: -50px; }
  .car-info__line--width-right {
    right: 0;
    height: 50px;
    width: 1px;
    bottom: -50px; }
  .car-info__line--width-left {
    left: 0;
    height: 50px;
    width: 1px;
    bottom: -50px; }
  .car-info__line--width-bottom {
    width: 100%;
    height: 1px;
    left: 0;
    right: 0;
    bottom: -50px; }
  .car-info__line--load-top {
    top: 0;
    width: 50px;
    height: 1px;
    right: -50px; }
  .car-info__line--load-bottom {
    top: 52px;
    width: 50px;
    height: 1px;
    right: -50px; }
  .car-info__line--load-right {
    height: 50px;
    top: 0;
    width: 1px;
    right: -50px; }

.reels {
  max-height: 830px;
  overflow-x: hidden;
  overflow-y: auto; }
  .reels__item {
    position: relative;
    background-color: #fff;
    margin: 10px 0;
    border: 3px solid transparent;
    cursor: pointer;
    transition: border-color 0.3s, padding 0.1s linear;
    width: 150px;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    user-select: none; }
    .reels__item-title {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      padding: 5px;
      background: rgba(255, 255, 255, 0.7);
      color: #333333;
      font-size: 13px;
      line-height: 1.05;
      text-align: center;
      pointer-events: none;
      user-select: none;
      opacity: 0;
      transition: opacity 0.2s ease; }
    .reels__item.active {
      padding-bottom: 55px; }
    .reels__item:hover, .reels__item.active {
      border-color: #a09268; }
      .reels__item:hover .reels__item-title, .reels__item.active .reels__item-title {
        opacity: 1; }
    .reels__item-icon {
      display: flex;
      align-items: center;
      justify-content: center; }
      .reels__item-icon img {
        display: block;
        width: 100%;
        height: 100%; }
    .reels__item-form {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      flex-wrap: wrap;
      box-sizing: border-box;
      padding: 0 5px 5px;
      background: #ffffff;
      display: none; }
      .reels__item-form input {
        width: calc(100% - 20px);
        height: 20px;
        border-radius: 0;
        text-align: center;
        -moz-appearance: textfield;
        box-sizing: border-box;
        padding: 0;
        border: 2px solid #a09268;
        border-right: 0; }
        .reels__item-form input::-webkit-outer-spin-button, .reels__item-form input::-webkit-inner-spin-button {
          -webkit-appearance: none;
          margin: 0; }
        .reels__item-form input:focus {
          outline: none; }
      .reels__item-form button {
        cursor: pointer; }
        .reels__item-form button[name="plus"] {
          width: 20px;
          height: 20px;
          box-sizing: border-box;
          color: #fff;
          background: #a09268;
          font-size: 16px;
          line-height: 16px;
          transition: background-color 0.3s ease;
          border: none;
          border-radius: 0; }
          .reels__item-form button[name="plus"]:hover {
            background: #8f825a; }
        .reels__item-form button[name="max"] {
          width: 100%;
          height: 25px;
          box-sizing: border-box;
          color: #333333;
          background: #e5d7af;
          font-size: 16px;
          line-height: 16px;
          border-radius: 0;
          border: 2px solid #a09268;
          transition: border-color 0.3s ease;
          margin-top: 5px; }
          .reels__item-form button[name="max"]:hover {
            border-color: #8f825a; }

.hide {
  display: none; }

.content-grid {
  position: relative;
  max-height: 100% !important;
  max-width: 100% !important; }
  .content-grid .reels__item-icon {
    display: block;
    position: absolute;
    z-index: 1;
    background: green; }
    .content-grid .reels__item-icon.muuri-item-dragging {
      z-index: 3; }
    .content-grid .reels__item-icon.muuri-item-releasing {
      z-index: 2; }
    .content-grid .reels__item-icon.muuri-item-hidden {
      z-index: 0; }
  .content-grid .muuri-item-placeholder {
    background: red;
